.main-container{
  display: flex;
  justify-content: center;
}
.main-wrapper{
  border: 1px solid blue;
  width: 25%;
}

.verify-wrapper{
  width: 25%;
}
.logo-wrap{

}
.phma-logo{
  width: 30%;
}
.user-image{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 8vh;
}

.user-image img{
  border-radius: 50%;
  height: 150px;
  width: 150px;
}

.user-name{
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 2vw;
}

.user-details{
  font-weight: 600;
  padding-left: 1vw;
  margin-top: 3vh;
}

.user-id{
  margin-bottom: 1vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2vw;
}

.id-wrap{
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 800;
  font-size: 1.3vw;
  margin-top: 2vh;
}

.id-number{
  background-color: yellow;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1vw;
  height: 5vh;
  width: 60%;
}
.qrcode{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 8vh;
  margin-bottom: 3vh;
}

.front-view{
}
.back-view{
  margin-top: 10vh;
  padding-top: 2vh;
}
.back-details{
}

.location{
  display: flex;  
  flex: 5;
  padding-left: 1vw;
}
.location-icon{
  flex: 0.3;
  display: flex;
  align-items: center;
}
.address{
  flex: 4.7;
  font-size: 1.1vw;
  display: flex;
  align-items: center;
  font-weight: 600;
  padding-left: 1vw;
  padding-left: 1vw;
}
.icon{
  font-size: 1.1vw;
}
.terms{
  margin-top: 10vh;
  padding-left: 1vw;
  padding-right: 1vw;
}
.terms p{
  text-align: center;
}
.signature{
}
.signature img{
  width: 90%;
}

.verify{

}
.verify-wrapper{

  /* display: flex; */
}
.verify-header{
  display: flex;
  justify-content: center;
  width: 100%;
  font-size: 2vw;
  font-weight: 700;
  color: #4BB543;
}

.verify-id-number{
  background-color: yellow;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1vw;
  height: 5vh;
  width: 60%;
  margin-bottom: 5vh;
}

@media screen and (min-width: 320px) {
  .verify-wrapper{
    width: 100%;
  }

  .user-image img{
    border-radius: 50%;
    height: 150px;
    width: 150px;
  }

  .user-name{
    font-weight: 700;
    font-size: 7vw;
  }

  .user-id{
    font-size: 5vw;
  }

  .verify-id-number{
    font-size: 5vw;
  }

  .verify-header{
    font-size: 8vw;
  }
  
}

@media screen and (min-width: 390px) {
  .verify-wrapper{
    width: 100%;
  }

  .user-image img{
    border-radius: 50%;
    height: 200px;
    width: 200px;
  }

  .user-name{
    font-weight: 700;
    font-size: 8vw;
  }

  .user-id{
    font-size: 6vw;
  }

  .verify-id-number{
    font-size: 6vw;
  }

  .verify-header{
    font-size: 8vw;
  }
  
}

@media screen and (min-width: 540px) {
  .verify-wrapper{
    width: 100%;
  }

  .user-image img{
    border-radius: 50%;
    height: 300px;
    width: 300px;
  }

  .user-name{
    font-weight: 700;
    font-size: 8vw;
  }

  .user-id{
    font-size: 5vw;
  }

  .verify-id-number{
    font-size: 6vw;
  }

  .verify-id-number{
    height: 8vh;
  }
  
}

@media screen and (min-width: 900) {
  .verify-id-number{
    height: 12vh;
  }

  .user-id{
    font-size: 4vw;
  }

  .phma-logo{
    width: 18%;
  }

  .user-image img{
    border-radius: 50%;
    height: 300px;
    width: 300px;
  }
}

@media screen and (min-width: 280px) {
  .verify-wrapper{
    width: 100%;
  }

  .user-image img{
    border-radius: 50%;
    height: 150px;
    width: 150px;
  }

  .user-name{
    font-weight: 700;
    font-size: 8vw;
  }

  .user-id{
    font-size: 5vw;
  }

  .verify-id-number{
    font-size: 6vw;
  }

  
}